<template>
  <div class="content-wrapper">
    <div class="container-fluid">
      <!--Start Dashboard Content-->
      <div class="row">
        <div class="col-12 col-lg-12">
          <div class="card">
            <div class="card-header text-capitalize">
              registered users
            </div>
            <div class="table-responsive">
              <table class="table align-items-center table-flush table-borderless">
                <thead>
                <tr>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Registration date</th>
                  <th>#Referred users</th>
                  <th>Total points earned</th>
                  <th>Action</th>
                </tr>
                </thead>

                <tbody>
                <tr v-for="(user, index) in users" :key="user.id">
                  <td>{{ user.name }}</td>
                  <td>{{ user.email }}</td>
                  <td>{{ user.created_at }}</td>
                  <td>{{ user.referrals_count }}</td>
                  <td>{{ user.total_points_earned }}</td>
                  <td>
                    <button class="btn btn-light btn-sm bg-danger-light2 mx-1" v-if="user.status === 'active'" @click="deactivate(user.id, index)"><i
                        class="icon-lock"></i> Block
                    </button>
                    <button class="btn btn-light btn-sm bg-success-light2 mx-1" v-else-if="user.status === 'blocked'" @click="activate(user.id, index)">
                      Activate
                    </button>
                    <button class="btn btn-light btn-sm bg-info-light2"><i class="icon-info"></i> Info</button>
                  </td>
                </tr>
                </tbody>
              </table>
              <nav aria-label="Page navigation example" class="float-right mt-3">
                <ul class="pagination">
                  <li class="page-item" :class="{'disable': !prev}" @click="getPrev">
                    <a class="page-link" href="#">
                    Previous
                  </a>
                  </li>
                  <li class="page-item" :class="{'disable': !next}" @click="getNext"><a class="page-link" href="#">
                    Next
                  </a></li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </div><!--End Row-->

      <!--End Dashboard Content-->

      <!--start overlay-->
      <div class="overlay toggle-menu"></div>
      <!--end overlay-->

    </div>
    <!-- End container-fluid-->

  </div>
</template>

<script>
export default {
  name: "UserDashboard",
  data() {
    return {
      users: [],
      next: null,
      prev: null,
      page: 1
    }
  },
  async mounted() {
    await this.getUsers(`/users?page=${this.page}`)
  },
  methods: {
    async getUsers(url) {
      this.$axios.get(url).then(response => {
        this.users = response.data.data.users
        this.next = parseInt(response.data.links.next?.toString().split('?page=', 2)[1])
        this.prev = parseInt(response.data.links.prev?.toString()?.split('?page=', 2)[1])
      })
    },
    async getNext() {
      await this.getUsers(`/users?page=${this.next}`)
    },
    async getPrev() {
      await this.getUsers(`/users?page=${this.prev}`)
    },
    async activate(id, index) {
      this.$axios.put(`/users/${id}`)
          .then(() => {
            this.users[index].status = 'active'
          })
    },
    async deactivate(id, index) {
      this.$axios.delete(`/users/${id}`)
          .then(() => {
            this.users[index].status = 'blocked'
          })
    }
  }
}
</script>

